.app-wrapper {
  margin: 0 auto;
  display: grid;
  grid-template-areas:
    'h h'
    'n c';
  width: 1200px;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  margin: 0;
  padding: 0;
  grid-area: c;
  background-color: aqua;
}
